.container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0 30px;
    align-self: center;
}

.container h1 {
    text-align: center;
    width: 100%;
    color: var(--main);
    margin-bottom: 40px;
    font-size: 2.5rem;
}

.container #vk_auth {
    width: min(400px, 100%);
}
